import React from 'react';
import './TransferList.css';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import axios from 'axios';
import { useEffect, useState } from 'react';
import $ from 'jquery';
import LazyLoad from 'react-lazy-load';
import Helmet from 'react-helmet';

const TransferList = () => {
    const[transferList, setTransferList] = useState();
    const[expandToggle, setExpandToggle] = useState(20);
    const[expandToggleLeagues, setExpandToggleLeagues] = useState(20);
    const[transferListLeagues, setTransferListLeagues] = useState();
    const[endpoint, setEndpoint] = useState();

    useEffect(() => {
        window.scrollTo(0, 0); // scroll top, when open page
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            $('#transferListPage .other').hide();

            await axios.get('/transfers/all')
            .then(response => {
                setTransferList(response.data && response.data.splice(0, expandToggle).map((e, i) => {
    
                    return <div className="col" key={`transferListOther` + i}>
                                <div>
                                    <div className='player'>
                                        <LazyLoad offset={800}><Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.name}><img loading="lazy" src={e.img} alt="playerImg" /></Tippy></LazyLoad>
                                        <img loading="lazy" src={e.flag} alt="flag" />
                                        <span>{e.position}</span>
                                    </div>
                                    <div className='fromTo'>
                                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.clubOutName}><img loading="lazy" src={e.clubOut} alt={e.clubOutName} /></Tippy>
                                        <span>‚Üí</span>
                                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.clubInName}><img loading="lazy" src={e.clubIn} alt={e.clubInName} /></Tippy>
                                    </div>
                                </div>
                                <div>
                                    <div className='date'>
                                        <span>{e.date}</span>
                                    </div>
                                    <div className='price'>
                                        <span>{e.price}</span>
                                    </div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, [expandToggle]);

    useEffect(() => {
        const fetchData = async () => {
            await axios.get(endpoint)
            .then(response => {
                setTransferListLeagues(response.data && response.data.splice(0, expandToggleLeagues).map((e, i) => {
                    return <div className="col" key={`transferListOther` + i}>
                                <div>
                                    <div className='player'>
                                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.name}><img loading="lazy" src={e.img} alt="playerImg" /></Tippy>
                                        <img loading="lazy" src={e.flag} alt="flag" />
                                        <span>{e.position}</span>
                                    </div>
                                    <div className='fromTo'>
                                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.clubOutName}><img loading="lazy" src={e.clubOut} alt={e.clubOutName} /></Tippy>
                                        <span>‚Üí</span>
                                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.clubInName}><img loading="lazy" src={e.clubIn} alt={e.clubInName} /></Tippy>
                                    </div>
                                </div>
                                <div>
                                    <div className='date'>
                                        <span>{e.date}</span>
                                    </div>
                                    <div className='price'>
                                        <span>{e.price}</span>
                                    </div>
                                </div>
                            </div>
                }));
            })
            .catch(err => {
                console.log(err);
            });
        }

        fetchData();
    }, [expandToggleLeagues, endpoint]);

    const transferToggle = () => {
        setExpandToggle(expandToggle + 20);
    }
    const transferToggleLeagues = () => {
        setExpandToggleLeagues(expandToggleLeagues + 20);
    }

    const selectleague = async (e) => {
        $('#transferListPage .other').show();
        $('#transferListPage .default').hide();
        setEndpoint(e.target.value);
        setExpandToggleLeagues(20);

        await axios.get(e.target.value)
        .then(response => {
            setTransferListLeagues(response.data && response.data.splice(0, expandToggleLeagues).map((e, i) => {
                return <div className="col" key={`transferListOther` + i}>
                            <div>
                                <div className='player'>
                                    <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.name}><img loading="lazy" src={e.img} alt="playerImg" /></Tippy>
                                    <img loading="lazy" src={e.flag} alt="flag" />
                                    <span>{e.position}</span>
                                </div>
                                <div className='fromTo'>
                                    <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.clubOutName}><img loading="lazy" src={e.clubOut} alt={e.clubOutName} /></Tippy>
                                    <span>‚Üí</span>
                                    <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content={e.clubInName}><img loading="lazy" src={e.clubIn} alt={e.clubInName} /></Tippy>
                                </div>
                            </div>
                            <div>
                                <div className='date'>
                                    <span>{e.date}</span>
                                </div>
                                <div className='price'>
                                    <span>{e.price}</span>
                                </div>
                            </div>
                        </div>
            }));
        })
        .catch(err => {
            console.log(err);
        });
    }

    return (
        <div id="transferListPage">
            <Helmet>
                <title>–°–ø–∏—Å–æ–∫ –≤—Å–µ—Ö —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–æ–≤ - –Ω–∞ Legendary Football</title>
                <meta name="description" content="–°–ø–∏—Å–æ–∫ –≤—Å–µ—Ö –ø—Ä–æ—à–µ–¥—à–∏—Ö —Ñ—É—Ç–±–æ–ª—å–Ω—ã—Ö —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–∞—Ö –≤ –æ–¥–Ω–æ–º –º–µ—Å—Ç–µ! –ë—É–¥—å—Ç–µ –≤ –∫—É—Ä—Å–µ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö –ø–µ—Ä–µ—Ö–æ–¥–æ–≤ –∑–≤–µ–∑–¥ –º–∏—Ä–æ–≤–æ–≥–æ —Ñ—É—Ç–±–æ–ª–∞ —Å –Ω–∞—à–∏–º –ø–æ–ª–Ω—ã–º —Å–ø–∏—Å–∫–æ–º —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–Ω—ã—Ö —Å–æ–±—ã—Ç–∏–π. –õ–µ—Ç–Ω–∏–µ –∏ –∑–∏–º–Ω–∏–µ –ø–µ—Ä–µ—Ö–æ–¥—ã - –≤—Å—ë –∑–¥–µ—Å—å." />
                <meta name="keywords" content="—Å–ø–∏—Å–æ–∫ –≤—Å–µ—Ö —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–æ–≤, —Ñ—É—Ç–±–æ–ª—å–Ω—ã–µ —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã, –ø–µ—Ä–µ—Ö–æ–¥—ã —Ñ—É—Ç–±–æ–ª–∏—Å—Ç–æ–≤, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–Ω–æ–µ –æ–∫–Ω–æ, –ª–µ—Ç–Ω–∏–µ —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã, –∑–∏–º–Ω–∏–µ —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã, –∞–∫—Ç—É–∞–ª—å–Ω—ã–µ —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–Ω–æ–µ –æ–∫–Ω–æ –≤ –µ–≤—Ä–æ–ø–µ, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã —Ä–ø–ª, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã –∞–ø–ª, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã —Å–µ—Ä–∏–∏ –∞, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã –±—É–Ω–¥–µ—Å–ª–∏–≥–∏, —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä—ã –ª–∏–≥–∏ 1" />
            </Helmet>
            <h1 className="pageName">–°–ø–∏—Å–æ–∫ —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–æ–≤ 
            <select onChange={selectleague} name="leagueSelection">
                <option value="/transfers/all">–í—Å–µ –ª–∏–≥–∏</option>
                <option value="/transfers/rpl">–†–ü–õ</option>
                <option value="/transfers/epl">–ê–ü–õ</option>
                <option value="/transfers/laliga">–õ–∞ –õ–∏–≥–∞</option>
                <option value="/transfers/seriea">–°–µ—Ä–∏—è –ê</option>
                <option value="/transfers/bundesliga">–ë—É–Ω–¥–µ—Å–ª–∏–≥–∞</option>
                <option value="/transfers/ligue1">–õ–∏–≥–∞ 1</option>
            </select></h1>
            <div className="listWrap">
                <div className="head">
                    <div>
                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content="–ò–≥—Ä–æ–∫"><span>–ò–≥—Ä–æ–∫</span></Tippy>
                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content="–ü–µ—Ä–µ—Ö–æ–¥—ã"><span>–û—Ç–∫—É–¥–∞ ‚Üí –ö—É–¥–∞</span></Tippy>
                    </div>
                    <div>
                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content="–î–∞—Ç–∞"><span>–î–∞—Ç–∞</span></Tippy>
                        <Tippy trigger={$(window).width() < 1024 ? 'click' : 'mouseenter'} content="–¶–µ–Ω–∞"><span>–¶–µ–Ω–∞</span></Tippy>
                    </div>
                </div>
                <div className="default">
                    {transferList}
                    <span onClick={transferToggle}>–†–∞–∑–≤–µ—Ä–Ω—É—Ç—å –µ—â—ë</span>
                </div>
                <div className="other">
                    {transferListLeagues}
                    <span onClick={transferToggleLeagues}>–†–∞–∑–≤–µ—Ä–Ω—É—Ç—å –µ—â—ë</span>
                </div>
            </div>
        </div>
    );
};

export default TransferList;